import {
  SET_USER,
  SET_USERS,
  SET_ERRORS,
  CLEAR_ERRORS,
  LOADING_UI,
  SET_UNAUTHENTICATED,
  LOADING_USER,
  GET_MATCHES,
  SET_SINGLE_USERS,
  DELETE_USER,
  UPDATE_BLOCK,
} from "../types";

import axios from "axios";
import { toast } from "react-toastify";

export const loginUser = (userData, history) => (dispatch) => {
  dispatch({ type: LOADING_UI });

  axios
    .post("/user/signIn", userData)
    .then((res) => {
      setAuthorizationHeader(res.data);
      history.push("/dashboard");
      dispatch({ type: CLEAR_ERRORS });
    })

    .catch((err) => {
      var errors = JSON.parse(err.response.data.error).errors
      dispatch({
        type: SET_ERRORS,
        payload: err.response ? err.response.data : err.response,
      });
      console.log(err)
      toast.error(`${errors[0].msg}`, {
        position: toast.POSITION.TOP_RIGHT,
      });
    });
};

export const updateProfile = (userData, history) => (dispatch) => {
  dispatch({ type: LOADING_UI });

  axios
    .post("/user/updateProfile", userData)
    .then((res) => {
      dispatch({
        type: SET_USERS,
        payload: res.data.data,
      });
      toast.success(`${res.data.message}`, {
        position: toast.POSITION.TOP_RIGHT,
      }); 
      dispatch({ type: CLEAR_ERRORS });
    })

    .catch((err) => {
      var errors = JSON.parse(err.response.data.error).errors
      dispatch({
        type: SET_ERRORS,
        payload: err.response ? err.response.data : err.response,
      });
      console.log(err)
      toast.error(`${errors[0].msg}`, {
        position: toast.POSITION.TOP_RIGHT,
      });
      
    });
};
